import { Link, useParams } from "react-router-dom";
import Carousel from 'react-bootstrap/Carousel';

const Details = (params) => {
    const { id } = useParams();
    const games = params.games.filter((game) => game.id === parseInt(id));
    console.log(games, id);
    return (
      <div className="details">
        <div className="container row" style={{
            margin: "auto",
            height: "100vh",
            paddingBottom: "2vw"
        }}>
            <div className="col-8">
                <div className="row" style={{
                    backgroundColor: "grey",
                    margin: "2vw 0vw",
                    padding: "2vw 2vw",
                    height: "60%",
                    textAlign: "left"
                }}>
                    <Carousel style={{
                        maxHeight: "765px"
                    }}>
                        <Carousel.Item interval={3000} >
                            <iframe className="w-100 d-block" src={games[0].vidURL} frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen style={{
                                width: "41.7vw",
                                height: "21.2vw"
                            }}>
                            </iframe>  
                            <Carousel.Caption>
                                <h3>{games[0].title} Trailer</h3>
                            </Carousel.Caption>
                        </Carousel.Item>
                        <Carousel.Item interval={3000}>
                            <img
                                className="d-block w-100"
                                src={games[0].cPic1}
                                alt="Second slide"
                            />
                            <Carousel.Caption>
                                <h3>Gameplay 1</h3>
                            </Carousel.Caption>
                        </Carousel.Item>
                        <Carousel.Item interval={3000}>
                            <img
                                className="d-block w-100"
                                src={games[0].cPic2}
                                alt="Third slide"
                            />
                            <Carousel.Caption>
                                <h3>Gameplay 2</h3>
                            </Carousel.Caption>
                        </Carousel.Item>
                    </Carousel>
                </div>
                <Link to="/Purchase" style={{
                    textDecoration: "none",
                    color: "black"
                }}>
                    <div className="row" style={{
                        backgroundColor: "grey",
                        margin: "2vw 0vw",
                        padding: "2vw 0vw",
                        height: "30%",
                        textAlign: "left"
                    }}>
                        <h4 style={{ padding: "1vw 2vw"}}>{games[0].description} <br/> game specifications and description. Add to cart button will also be here, click here to purchase</h4>
                    </div>
                </Link>
            </div>
            <div className="col-4" style={{
                backgroundColor: "grey",
                margin: "2vw 0vw 1vw 0vw",
                padding: "2vw 0vw",
                height: "100%",
                textAlign: "left"
            }}>
                <img src={games[0].pic} style={{ width: "15vw", marginLeft: "3.5vw" }}/>
                <h5 style={{marginLeft: "2vw"}}> <br /> Publisher: {games[0].publisher} <br />Developer: {games[0].developer} <br />game categories/ratings and icon</h5>
            </div>
        </div>
      </div>
    );
  }
   
  export default Details;