import logo from './logo.svg';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navbar from './Navbar';
import Home from './Home';
import Action from './Action';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Browse from './Browse';
import Details from './Details';
import Purchase from './Purchase';
import Confirmation from './Confirmation';
import Footer from './Footer';
import { useState } from 'react';
import ssmgPic from './SSMG.jpg';
import alPic from './AL.jpg';
import hkPic from './HK.jpg';
import valPic from './Val.jpg';
import outlastPic from './outlast.jpg';
import wideMario from "./marioGP.jpg";
import wideMario2 from './marioGP2.jpg';
import CartContextProvider from './context/cartContext';
import valGP from './valGP.jpg';
import valGP2 from './valGP2.jpg';
import alGP from './alGP.jpg';
import alGP2 from './alGP2.jpg';
import hkGP from './hkGP.jpg';
import hkGP2 from './hkGP2.jpg';
import outGP from './outGP.jpg';
import outGP2 from './outGP2.jpg';


function App() {
  const [games, setGames] = useState([
    { title: 'Super Mario Galaxy', genre: 'adventure', description: 'Gucci game for gucci people', publisher: 'Nintendo', developer: 'Nintendo EAD Tokyo', price: '0', pic: ssmgPic, cPic1: wideMario, cPic2: wideMario2, vidURL: "https://www.youtube.com/embed/rmN8DHZYNCg", id: 0},
    { title: 'Apex Legends', genre: 'action', description: 'the best game of all time', publisher: 'Electronic Arts', developer: 'Respawn Entertainment', price: '0', pic: alPic, cPic1: alGP, cPic2: alGP2, vidURL: "https://www.youtube.com/embed/cheYIVEtVQ4", id: 1},
    { title: 'Hollow Knight', genre: 'adventure', description: 'The Best Game ever after apex', publisher: 'Team Cherry', developer: 'Team Cherry', price: '16.99', pic: hkPic, cPic1: hkGP, cPic2: hkGP2, vidURL: "https://www.youtube.com/embed/UAO2urG23S4", id: 2},
    { title: 'Valorant', genre: 'action', description: 'the league enjoyer\'s CS:GO', publisher: 'Riot Games', developer: 'Riot Games', price: '0', pic: valPic, cPic1: valGP, cPic2: valGP2, vidURL: "https://www.youtube.com/embed/UMzKD73cs3c", id: 3},
    { title: 'Outlast', genre: 'horror', description: 'scary game to scare people', publisher: 'Red Barrels', developer: 'Red Barrels', price: '22.79', pic: outlastPic, cPic1: outGP, cPic2: outGP2, vidURL: "https://www.youtube.com/embed/uKA-IA4locM", id: 4}
  ]);
  const addGame = () => {
    var listOfGames = Array.from(games);
    listOfGames.push({
      title: "wow",
      genre: "wow",
      description: "wow",
      publisher: "wow",
      developer: "wow",
      price: "wow",
      id: "5"
  })
    setGames(listOfGames);
  }
  return (
    <Router>
      <div className="App">
        <CartContextProvider>
          <Navbar/>
            <Switch>
              <Route exact path="/Project2">
                <Home />
              </Route>
              <Route exact path="/Action">
                <Action />
              </Route>
              <Route path="/Browse/:genre">
                <Browse games={games} addGame={addGame} />
              </Route>
              <Route path="/Details/:id">
                <Details games = {games} />
              </Route>
              <Route path="/Purchase">
                <Purchase />
              </Route>
              <Route path="/Confirmation">
                <Confirmation />
              </Route>
            </Switch>
          <Footer />
        </CartContextProvider>
      </div>
    </Router>
  );
}

export default App;
