import Carousel from 'react-bootstrap/Carousel';
import ssmgPic from './SSMG.jpg';
import alPic from './AL.jpg';
import hkPic from './HK.jpg';
import valPic from './Val.jpg';
import outlastPic from './outlast.jpg';
import wideSSMG from './wideSSMG.jpg';
import wideVal from './wideVal.jpg';
import wideApex from './wideApex.jpg';
import widerApex from './widerApex.jpg';
import superwideVal from './superwideVal.jpg';
import { Link } from 'react-router-dom';

const Home = () => {
  return (
    <div className="home">
      <div className="container-sm" style={{
        backgroundColor: "#D9D9D9",
        padding: "2vw 2vw",
        marginTop: "3vw",
        marginBottom: "2vw",
        maxHeight: "765px"
      }}>
        <Carousel style={{
          maxHeight: "765px"
        }}>
          <Carousel.Item interval={3000} >
            <Link to="/Details/0">
              <img
                className="d-block w-100"
                src={wideSSMG}
                alt="First slide"
              />  
              <Carousel.Caption>
                <h3>Super Mario Galaxy</h3>
              </Carousel.Caption>
            </Link>
          </Carousel.Item>
          <Carousel.Item interval={3000}>
            <Link to="/Details/3">
              <img
                className="d-block w-100"
                src={wideVal}
                alt="Second slide"
              />
              <Carousel.Caption>
                <h3>Valorant</h3>
              </Carousel.Caption>
            </Link>
          </Carousel.Item>
          <Carousel.Item interval={3000}>
            <Link to="/Details/1">
              <img
                className="d-block w-100"
                src={widerApex}
                alt="Third slide"
              />
              <Carousel.Caption>
                <h3>Apex Legends</h3>
              </Carousel.Caption>
            </Link>
          </Carousel.Item>
        </Carousel>
      </div>
      <div className="container-sm" style={{
        backgroundColor: "#D9D9D9",
        padding: "3vw 5vw",
        marginTop: "1vw"
      }}>
        <img src={superwideVal} style={{
          height: "15vw"
        }}/>
        <h3 style={{
          position: "relative",
          fontSize: "3vw",
          fontWeight: "bold"
        }}>50% Off On In-Game Purchases</h3>
      </div>
      <div className="container-sm" style={{
        backgroundColor: "#D9D9D9",
        padding: "15vw 5vw",
        marginTop: "4vw",
        marginBottom: "5vw",
        width: "50%"
      }}>
        <h1>Popular Games</h1>
      </div>
    </div>
  );
}
 
export default Home;